@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap&subset=cyrillic);
.header {
  background-color: #FFFFFF;
  padding: 34px 30px;
  margin-top: 50px;
  margin-bottom: 20px;
}

.header__heading {
  text-transform: uppercase;
  font-size: 24px;
  font-weight: bold;
  margin:0;
  color: #000;
}

.header__list {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
  margin:0;
  padding: 0;
}

.header-link {
  color: #000;
  font-size: 13px;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.3);
  -webkit-transition: color 0.3s ease, border 0.3s ease;
  transition: color 0.3s ease, border 0.3s ease;
}

.header-link:hover {
  color: #000;
  text-decoration: none;
  border-bottom: 2px solid #66D37E;
}

.form-auhorization {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 500px;
  margin: 0 auto;
}

.form-auhorization__input {
  height: 65px;
  border: 1px solid #cdd0d1;
  border-radius: 9px;
  background-color: transparent;
  margin-bottom: 10px;
  font-weight: 200;
  width: 100%;
  font-size: 1rem;
  padding: 10px 20px;
  outline: none;
  cursor: pointer;
  -webkit-transition: border-color 0.3s ease;
  transition: border-color 0.3s ease;
}

.form-auhorization__input:hover {
  border-color: #939da0;
}

.form-auhorization__input:focus {
  border-color: #939da0;
}


.form-auhorization__error--message {
  color: #DF5868;
  font-size: 15px;
  font-weight: bold;
  margin-bottom: 5px;
}

@media screen and (max-width: 601px) {
  .form-auhorization {
    width: 92%;
  }
}
