.home-page-form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 500px;
  margin: 0 auto;
}

.home-page-form__label {
  color: #000;
  margin-top:10px;
  font-size:20px;
  font-weight: bold;
}

.btn-exit {
  box-shadow: 4px 6px 10px rgba(241,106,81,.4);
  color: #FFFFFF;
  font-weight: 700;
  background-color: #E06149;
  padding: 20px 65px;
  border: none;
  outline: none;
  border-radius: 5px;
  margin-top:10px;
  -webkit-transition:  background-color 0.3s ease;
  transition:  background-color 0.3s ease;
}

.btn-exit:hover {
  background-color: rgb(196, 124, 111);
}
* {
  box-sizing: border-box;
}

body {
  background-color:#E8E8E8;
  font-family: "Open Sans", sans-serif;
}

.wrapper-page {
  background-color: #FFFFFF;
  padding: 50px 0 50px 0;
}

.wrapper-page__heading {
  color: #000;
  font-weight: 600;
  font-size: 40px;
  text-align: center;
  padding: 50px 0 0 20px;
}

.wrapper-page__text {
  text-align: center;
  max-width: 500px;
  margin: 15px auto;
  font-weight: 500;
}

.btn-submit {
  box-shadow: 4px 6px 10px rgba(241,106,81,.4);
  color: #FFFFFF;
  font-weight: 700;
  background-color: #E06149;
  padding: 20px 65px;
  border: none;
  outline: none;
  border-radius: 5px;
  margin-top:10px;
  -webkit-transition:  background-color 0.3s ease;
  transition:  background-color 0.3s ease;
}

.btn-submit:hover {
  background-color: rgb(196, 124, 111);
}

.btn-standart {
  margin-top: 20px;
  color: #333;
  border: 2px solid #333;
  border-radius: 5px;
  padding: 10px 20px;
  background-color: transparent;
  font-weight: 600;
  -webkit-transition: border-color 0.3s ease, color 0.3s ease;
  transition: border-color 0.3s ease, color 0.3s ease;
  outline: none;
}

.btn-standart:hover {
  border-color: #999;
  color: #999;
}

.successes-message {
  color: #34A853;
  font-size: 15px;
  font-weight: 600;
  margin-top: 10px;
}
